<p><span class="rvts58">水元素学派（</span><span class="rvts59">Hydrosophist</span><span class="rvts58">）</span><span class="rvts59">:</span>
<br><img width="128" height="128" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber218.jpg"><span class="rvts28"></span></p>
<p><span class="rvts21">水元素学派技能一共有</span><span class="rvts20">22</span><span class="rvts21">个。所有的水元素学派技能都基于角色的技能等级得到额外的伤害加成或治疗加成。水元素学派中含有众多的治疗技能可以使角色充当治疗职业。</span>
<br><span class="rvts21">除此之外，水元素技能中有些技能可以转化水或血的区域变为冰，对敌人造成水元素伤害。也可以利用水元素结合毒元素，对敌人制造陷阱。</span>
<br><span class="rvts43"></span>
<br><span class="rvts29">水元素学派技能书可以从以下</span><span class="rvts33">NPC</span><span class="rvts29">中买到（或学到）：</span>
<br><span class="rvts20">1. </span><span class="rvts21">赖齐克（</span><span class="rvts20">Rezik</span><span class="rvts21">）</span><span class="rvts20"> --牋</span><span class="rvts21">欢乐堡</span>
<br><span class="rvts20">2. </span><span class="rvts21">商人布雷（</span><span class="rvts20">Trader Bree</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">浮木城</span>
<br><span class="rvts20">3. </span><span class="rvts21">西蒙（</span><span class="rvts20">Simone</span><span class="rvts21">）</span><span class="rvts20"> --</span><span class="rvts21">阿玛迪亚避难所</span>
<br><span class="rvts20">4. </span><span class="rvts21">精灵暴风吟唱者（</span><span class="rvts20">Elf StormChanter</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">精灵营地，死神海岸，无名岛</span>
<br><span class="rvts20">5.</span><span class="rvts21">阿尔麦拉（</span><span class="rvts20">Almira</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">复仇女士号</span>
<br><span class="rvts20">6. </span><span class="rvts21">蜥蜴做梦者（</span><span class="rvts20">Lizard Dreamer</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">费勒斯神庙南部，无名岛</span>
<br><span class="rvts20">7.</span><span class="rvts21">商人弗利尔（</span><span class="rvts20">Trader Friel</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">阿克斯</span>
<br><span class="rvts20">8.</span><span class="rvts21">商人阿若瓦伊（</span><span class="rvts20">Trader Avarae</span><span class="rvts21">）</span><span class="rvts39"></span><span class="rvts20">-- </span><span class="rvts21">阿克斯</span>
